<template>
  <div class="bg"   :style="bgStyle(true)" id="info">
    <v-touch
      @swipedown="moveToParent"
      @swipeleft="moveToSibling(true)"
      @swiperight="moveToSibling(false)">
      <div id="controls"> 
        <div id="control-row">
          <div id="direction" v-if="parent" @click="moveToParent">
            <i style="font-size: 3.2em; color: black; text-align: center;" class="fas fa-arrow-alt-circle-up" />
          </div>
        </div>
        <div id="control-row">
          <div id="direction" v-if="parent && parent.children.length > 1" @click="moveToSibling(false)">
            <i style="font-size: 3.2em; color: black; text-align: center;" class="fas fa-arrow-alt-circle-left" />
          </div>
          <div id="direction" v-if="parent && parent.children.length > 1" @click="moveToSibling(true)">
            <i style="font-size: 3.2em; color: black; text-align: center;" class="fas fa-arrow-alt-circle-right" />
          </div>
        </div>
      </div>
        <transition :name="transitionOrientation" mode="out-in">
          <Node
            :node="node"
            :key="node.id"
            @moveToChild="moveToChild"
            :selectedLanguage="languageIndex"
            :style="bgStyle(false)"
            />
        </transition>
        <sound
          class=" controldiv navButton"
          v-if="node.audio[languageIndex]"
          :sound="node.audio[languageIndex]"/>

        <img
            class="navButton language-btn"
            type="image"
            @click="changeLanguage()"
            :src="'../../static/' + selectedLanguage + '-icon.svg'"
            />
    </v-touch>
    <div id="myModal" class="modal" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          <div class="modal-body">
            <p>Please turn your phone</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Node from '../components/Node.vue'
import Auth from '../auth'
import Sound from '../components/Sound'
import nodemixin from '../mixins/nodemixins'
import langmixin from '../mixins/languagemixins'
import PathStack from '../components/PathStack'
import { getJSONfromS3 } from '../services/jsonService'
import EventBus from '../utils/eventBus'

export default {
  name: 'info',
  components: {
    Node,
    Sound,
    PathStack
  },
  mixins: [nodemixin, langmixin],
  data () {
    return {
      /* eslint-disable */
       loggedIn: false,
       transitionOrientation: 'slidedown',
       animations: ['slideleft', 'slideright', 'slideup', 'slidedown'],
       deleteMode: false,
       user: Auth.user,
       path: [],
       pushed: false,
    }
    /* eslint-enable */
  },
  methods: {
    showModalIfNotPortrait(angle){
      if(angle === 0){
        $('#myModal').modal('hide');
      }else{
        $('#myModal').modal('show');
      }
    }
  },
  watch: {
    languageIndex () {
      this.$router.push({
        name: 'info',
        params: {
          language: this.languages[this.languageIndex]
        }
      })
    }
  },
  created: function () {
    getJSONfromS3()
      .then(res => {
        this.node = res
        const audioFile = this.node.audio[this.languageIndex]
        if (audioFile) {
          EventBus.$emit('playAudio', audioFile)
        }
      })
    history.pushState(null, document.title, location.href)
    window.addEventListener('popstate', (event) => {
      history.pushState(null, document.title, location.href)
    })

    this.showModalIfNotPortrait(window.screen.orientation.angle)
    window.addEventListener('orientationchange', () => {
        this.showModalIfNotPortrait(window.screen.orientation.angle)
    });
  }
}
</script>



