<template>
<div style="height: 100vh">
  <div :key="node.id" class="container" v-if="hasChildren">
    <div
      v-for="(n, index) in node.children"
      :key="n.id"
      class="btn col-4 iconarea">
       <v-touch

        ref="tapper"
        @tap="(e) => handleTap(e, index, n.id)"
        @press="onPress(index)"
        :press-options="{ time: 200 }"
        :tap-options="{ time: 250 }"
        style="width: 100%; height: 100%;">
        <div
          :id="n.id"
          class="btn box">
          <i v-if="n.url" class="icontext fas fa-external-link-alt"></i>
          <img
            class="icon"
            draggable="false"
            :src="iconUrl(n.icon)"
            @contextmenu.prevent="{}"
            @dragenter.prevent="(e) => handleDragEnter(e, n.id)"
            @dragleave.prevent="(e) => handleDragLeave(e, n.id)"
            @drop.prevent="(e) => handleDrop(e, index, n.id)"
            @dragover.prevent="{}">
        </div>
      </v-touch>
    </div>
  </div>
  <div v-if="emptyNode" style="padding-top: 15vh; font-size: 20px">Ei mitn nytettv.</div>
  <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-lg modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-body">
            <iframe
              class="maps"
              frameborder="0" style="border:0"
              :src="mapsUrl" allowfullscreen>
            </iframe>
          </div>
        </div>
      </div>
    </div>
  <div class="bottom mapIcon" style="cursor: pointer;" v-if="node.location">
    <i style="font-size: 8vh" class=" fas fa-compass" data-toggle="modal" data-target="#exampleModal"></i>
  </div>
</div>
</template>

<script>
import EventBus from '../utils/eventBus'

export default {
  name: 'Node',
  props: [
    'transitionOrientation',
    'node',
    'selectedLanguage',
    'deleteMode'
  ],
  computed: {
    mapsUrl () {
      return 'https://www.google.com/maps/embed/v1/place?key=' + process.env.MAPS_KEY + '&q=' + this.node.location
    },
    hasChildren () {
      return (this.node.children && this.node.children.length > 0)
    },
    emptyNode () {
      return this.node.children.length < 1 && this.node.links.length < 1
    }
  },
  methods: {
    deleteLink: function (index) {
      this.$emit('deleteLink', index)
    },
    iconUrl: function (route) {
      return 'https://' + process.env.RESOURCEBUCKET + '.s3.eu-west-1.amazonaws.com/public/' + route
    },
    handleTap: function (e, index) {
      if (this.deleteMode) {
        this.$emit('deleteChild', index)
      } else {
        this.$emit('moveToChild', index)
      }
    },
    handleDragEnter (e, id) {
      document.getElementById(id).style.background = 'orange'
    },
    handleDragLeave (e, id) {
      document.getElementById(id).style.background = 'rgba(95, 157, 159,0.6)'
    },
    handleDrop (e, index, id) {
      const file = e.dataTransfer.items[0].getAsFile()
      document.getElementById(id).style.background = 'rgba(95, 157, 159,0.6)'

      file.type.split('/')[0] === 'audio'
        ? this.$emit('audioPreviewUpload', file, index)
        : this.$emit('iconUpdate', file, index)
    },
    onPress (index) {
      EventBus.$emit('playAudio', this.node.children[index].previewAudio[this.selectedLanguage])
    }
  }

}
</script>

<style scoped>
.mapIcon {
  width: 100vw;
  outline: none;
  position: fixed;
  bottom: 5vh;
}

.maps{
  height: 50vh;
  width: 100%;
}
.icontext{
  margin-right: 4px;
  position: absolute;
  right: 0;
  font-size: 2vw;
  opacity: 0.5;
}
.box {
    width: 18vw;
    height: 18vw;
    max-width: 18vh;
    max-height: 18vh;
    position: relative;
}
.box:hover {
  background: rgba(95, 157, 159,0.9);
}
#audio {
  color: yellow;
}
.icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 10vh;
  max-height: 12vw;
}
.loader {
    border: 5px dotted rgb(2, 39, 107);
    border-radius: 30px;
    height: 50px;
    left: 50%;
    position: absolute;
    top: 25%;
    width: 50px;
    animation: pulse 1.5s ease-out;
    animation-iteration-count: infinite;
}

@keyframes pulse {
    0% {
      transform: scale(.1);
      opacity: 0.0;
    }
    50% {
      opacity: 1;
    }
    100% {
      transform: scale(1.2);
      opacity: 0;
    }
}
</style>



