<template>
  <div class="app">
    <audio id="player"/>
    <main class="main">
        <router-view></router-view>
    </main>
  </div>
</template>

<script>
import nodemixin from './mixins/nodemixins'
import EventBus from './utils/eventBus'
import { getJSONfromS3 } from './services/jsonService'

let player

export default {
  name: 'App',
  mixins: [nodemixin],
  mounted () {
    EventBus.$on('initAudio', function (payLoad) {
      player = document.getElementById('player')
      player.play()
    })
    EventBus.$on('playAudio', function (input) {
      if (input) {
        player.src = 'https://s3-eu-west-1.amazonaws.com/' + process.env.RESOURCEBUCKET + '/public/' + input
        player.play()
      }
    })
  },

  created: function () {
    getJSONfromS3()
      .then(res => {
        this.node = res
        const audioFile = this.node.audio[this.languageIndex]
        if (audioFile) {
          player.src = 'https://s3-eu-west-1.amazonaws.com/' + process.env.RESOURCEBUCKET + '/public/' + audioFile
          player.play()
        }
      })
  }
}
</script>

<style>
.nopadding {
  padding-left: 0px;
  padding: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 98vw;
  height: 100vh;
}
</style>



