<template>
  <div class="bg container.fluid" :style="bgStyle(true)"
 id="info" >
    <div class="row" style="max-height: 100vh">
      <div class="col-xl-2 col-md-3 col-sm-4 col-5" style="padding-left: 0px; padding-right: 0px; height: 100vh; background-color: #C0C0C0; min-width: 180px">
        <admin-controls
          v-if="user.authenticated"
          :node="node"
          @addChild="addChild"
          @getJSON="getJSON"
          @addContent="addContent"
          :selectedLanguage="languageIndex"
          :deleteMode="deleteMode"
          @toggleDeleteMode="toggleDeleteMode"
          :unloadListener="unloadListener"
          @addLocation="addLocation"/>
      </div>
      <div class="col-xl-10 col-md-9 col-sm-8 col-7" style="padding-left: 0px; padding-right: 0px;">
        <v-touch v-on:swipedown="moveToParent" v-on:swipeleft="moveToSibling(true)" v-on:swiperight="moveToSibling(false)">
          <transition :name="transitionOrientation" mode="out-in">
            <Node
              :node="node"
              :key="node.id"
              @moveToChild="moveToChild"
              :selectedLanguage="languageIndex"
              :deleteMode="deleteMode"
              @deleteChild="deleteChild"
              @deleteLink="deleteLink"
              @audioPreviewUpload="audioPreviewUpload"
              @iconUpdate="updateIcon"
              :style="bgStyle(false)"
              />
            </transition>
            <div class="row">
          </div>
          <div id="controls">
            <div id="control-row">
              <div id="direction" v-if="parent" @click="moveToParent">
                <i style="font-size: 3.2em; color: black; text-align: center;" class="fas fa-arrow-alt-circle-up" />
              </div>
            </div>
            <div id="control-row">
              <div id="direction" v-if="parent && parent.children.length > 1" @click="moveToSibling(false)">
                <i style="font-size: 3.2em; color: black; text-align: center;" class="fas fa-arrow-alt-circle-left" />
              </div>
              <div id="direction" v-if="parent && parent.children.length > 1" @click="moveToSibling(true)">
                <i style="font-size: 3.2em; color: black; text-align: center;" class="fas fa-arrow-alt-circle-right" />
              </div>
            </div>
          </div>
            <sound
              class=" controldiv navButton"
              v-if="node.audio[languageIndex]"
              :sound="node.audio[languageIndex]"
              :deleteMode="deleteMode"
              @deleteSound="deleteSound"/>
          <img
            class="language-btn"
            type="image"
            @click="changeLanguage()"
            :src="'../../static/'+ selectedLanguage +'-icon.svg'"/>
        </v-touch>
      </div>
      <div class="col-xl-1 col-md-1 col-sm-1 col-1" />
    </div>
  </div>
</template>
<script>
import Node from '../components/Node.vue'
import AdminControls from '../components/AdminControls'
import { saveJSONTree, saveAdminJSON, getAdminJSON } from '../services/jsonService'
import {isRootNode} from '../utils'
import Auth from '../auth'
import Sound from '../components/Sound'
import nodemixin from '../mixins/nodemixins'
import langmixin from '../mixins/languagemixins'
import PathStack from '../components/PathStack'
import { saveAudio } from '../services/audioService'
import { saveIcon } from '../services/iconService'
import EventBus from '../utils/eventBus'

export default {
  name: 'Admin',
  components: {
    Node,
    AdminControls,
    Sound,
    PathStack
  },
  mixins: [nodemixin, langmixin],
  methods: {
    getJSON: function (target) {
      let tempNode = this.node
      while (isRootNode(tempNode.id)) {
        tempNode = this.parent
        this.parent = this.parent.parent
      }
      this.deleteParents(tempNode)
      this.parent = null
      if (target === 'publish') {
        saveJSONTree(JSON.stringify(tempNode))
      } else {
        saveAdminJSON(JSON.stringify(tempNode))
      }
      this.node = tempNode
      this.path = []
    },
    deleteParents (node) {
      // eslint-disable-next-line
      node.parent ? delete node.parent : {}
      if (!node.children) {
        return node
      }
      node.children.forEach(child => {
        this.deleteParents(child)
      })
    },
    addChild: function (child) {
      this.node.children.push(child)
    },
    addContent: function (content) {
      this.node = Object.assign({}, this.node, content)
    },
    toggleDeleteMode: function () {
      this.deleteMode = !this.deleteMode
    },
    deleteChild: function (index) {
      if (!!this.node.children.length || !!this.node.children[index].children.length) {
        this.node.children.splice(index, 1)
      }
    },
    deleteLink: function (index) {
      this.node.links.splice(index, 1)
    },
    deleteSound: function () {
      this.node.audio.splice(this.languageIndex, 1, '')
    },
    audioPreviewUpload: function (file, index) {
      if (file && file.size > 400000) {
        alert('Suurin sallittu tiedosto koko on 100kb')
        return
      }
      saveAudio(file).then((res) => {
        this.node.children[index].previewAudio[this.languageIndex] = res.key
      })
    },
    addLocation (location) {
      this.node.location = location
    },
    updateIcon (file, index) {
      if (file && file.size > 50000) {
        alert('Suurin sallittu tiedosto koko on 50kb')
        return
      }
      saveIcon(file).then((res) => {
        this.node.children[index].icon = res.key
      })
    }
  },
  data () {
    return {
      /* eslint-disable */
       transitionOrientation: 'slidedown',
       deleteMode: false,
       user: Auth.user,
       path: [],
       unloadListener: null
    }
    /* eslint-enable */
  },
  created () {
    EventBus.$emit('initAudio', {})
    this.unloadListener = function (e) {
      const confirmationMessage = 'Olethan tallentanut muutoksesi';
      (e || window.event).returnValue = confirmationMessage // Gecko + IE
      return confirmationMessage // Gecko + Webkit, Safari, Chrome etc.
    }
    window.addEventListener('beforeunload', this.unloadListener)
    getAdminJSON()
      .then(res => {
        this.node = res
        const audioFile = this.node.audio[this.languageIndex]
        if (audioFile) EventBus.$emit('playAudio', audioFile)
      })
  }
}
</script>
<style>
#info {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  position: relative;
}
.mapIcon {
  left: 0px;
  outline: none;
  position: fixed;
  bottom: 20px;
}
</style>



