<template>
  <div>
    <audio id="audio">
      <source :src="soundUrl">
      Your browser does not support the audio element.
    </audio>
    <div class="container" @click="playAudio">
      <i style="font-size: 8vh;" :class="'fas fa-volume-up'" @click="playAudio" />
    </div>
  </div>
</template>

<script>
export default {
  props: [
    'sound',
    'deleteMode'
  ],
  methods: {
    playAudio: function () {
      if (this.deleteMode) {
        this.$emit('deleteSound')
      } else {
        document.getElementById('audio').load()
        document.getElementById('audio').play()
      }
    },
    iconSize: function () {
      return document.documentElement.clientHeight > 1000 ? 6 : 3
    }
  },
  computed: {
    soundUrl: function () {
    return 'https://' + process.env.RESOURCEBUCKET + '.s3.eu-west-1.amazonaws.com/public/' + this.sound
    }
  }  
}
</script>



