import Vue from 'vue'
import Router from 'vue-router'
import { Auth, Logger } from 'aws-amplify'
import LandingPage from '../views/LandingPage'
import AdminPage from '../views/AdminPage'
import InfoPage from '../views/InfoPage'
import Login from '../views/Login'
import ForgotPassword from '../views/ForgotPassword'
import AuthService from '../auth'
const logger = new Logger('AuthRouter')

Vue.use(Router)

const AuthFilter = (to, from, next) => {
  logger.debug('before routing ', to, from)
  Auth.currentAuthenticatedUser()
    .then(user => {
      logger.debug('...has user', user)
      AuthService.setUser(user)
      Auth.currentCredentials()
        .then(credentials => {
          AuthService.setUserId(credentials.identityId)
        })
        .catch(err => logger.debug('get current credentials err', err))
      next()
    })
    .catch(err => {
      logger.debug('...no user', err)
      AuthService.setUser(null)
      to.name.startsWith('auth') ? next() : next('/login')
    })
}
export default new Router({
  routes: [
    {
      path: '/',
      name: 'root',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: LandingPage
    },
    {
      path: '/admin',
      name: 'Admin',
      component: AdminPage,
      beforeEnter: AuthFilter
    },
    {
      path: '/info/:language',
      name: 'info',
      component: InfoPage,
      props: true,
      beforeEnter: (to, from, next) => {
        from.name === 'Home' ? next() : next('/')
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/forgot',
      name: 'forgot',
      component: ForgotPassword
    },
    {
      path: '*',
      redirect: { name: 'root' }
    }
  ]
})



